@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
* {
  box-sizing: border-box;
}

html {
  border: 15px solid #00b4d8;
}

body {
  font-family: 'Fira Sans', sans-serif;
}

.App {
  min-height: 100vh;
  background: white;
}

.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

.title {
  font-family: 'Satisfy', sans-serif;
  color: #00b4d8;
  font-weight: 200;
  font-size: 3rem;
}

.search-form {
  min-height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.search-filter {
  max-width: 500px;
  margin: 0 auto;
}

.recipe {
  border-radius: 60px;
  padding: 20px 0;
  margin: 20px;
  width: 400px;
  box-shadow: 8px 5px 5px 2px #00b4d8;
  letter-spacing: 1px;
  border: 1px solid #00b4d8;
}

.recipe:hover,
.recipe:active {
  transform: scale(1.02);
  transition: transform 200ms ease-in-out;
}

.recipe_title {
  text-align: center;
  font-size: 1.6rem;
  color: #565656;
}

.recipe h2 {
  margin: 0;
  padding: 10px 20px 0px 20px;
  color: #565656;
  font-size: 1.3rem;
}

.image_div {
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
}

.image {
  display: block;
  width: 100%;
  margin: -33% 0;
}

.calories {
  text-align: center;
}

.totalTime {
  text-align: center;
}

.ingredients {
  list-style-type: none;
  padding: 10px 20px;
}

.ingredient {
  padding: 5px 0;
  line-height: 1.4rem;
}

.ingredient i {
  color: #00b4d8;
}

.url {
  text-align: center;
}

.search-bar {
  max-width: 320px;
  min-width: 220px;
  width: 45%;
  border: none;
  padding: 12px;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
}

.search-bar::-webkit-input-placeholder {
  color: #00000070;
}

.search-bar:-ms-input-placeholder {
  color: #00000070;
}

.search-bar::placeholder {
  color: #00000070;
}

.search-button {
  background-color: #00b4d8;
  border: 1px solid rgb(170, 170, 170);
  padding: 12px 15px;
  color: white;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
  position: relative;
  right: 43px;
  top: -1px;
}

.search-bar:focus,
.search-bar:hover {
  box-shadow: 0 0 5px 2px #00000040;
  outline: none;
}

.search-button:focus {
  outline: none;
}

.filters {
  -webkit-columns: auto;
          columns: auto;
  text-align: center;
  padding: 1rem 1rem 0 1rem;
}

.filter {
  display: inline-block;
  width: auto;
  margin: 0.25rem;
  min-height: 0;
  overflow: hidden;
  max-width: 100%;
  border: 1px solid rgb(170, 170, 170);
  padding: 8px 15px;
  border-radius: 80px;
  color: #111;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  cursor: pointer;
}

.active-filter {
  background: #00b4d8;
  color: #fff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

